import "./Card.css";
import { head, cloudPlans } from "./Pricing_db";
function Card(props) {
  return (
    <div className="card_body">
      <h3>{props.plan}</h3>
      <div className="price">
        <h2>{props.price}</h2>
      </div>
      <div className="text">
        <p>{props.text}</p>
      </div>
      <hr />

      <div className="features">
        <h6>
          <span>✔</span> &nbsp;
          {props.storage}
        </h6>
        <h6>
          <span>✔</span> &nbsp;
          {props.dataTransfer}
        </h6>
        <h6>
          <span>✔</span> &nbsp;
          {props.panel}
        </h6>
        <h6>
          <span>✔</span> &nbsp;
          {props.support}
        </h6>
        <h6>
          <span>✔</span> &nbsp;
          {props.themes}
        </h6>
        <h6>
          <span>✔</span> &nbsp;
          {props.others}
        </h6>
      </div>
      <div>
        <button>{props.tag}</button>
      </div>
    </div>
  );
}

export default Card;
